<!--
 * @Description: 角色详情信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-10-09 16:51:37
 -->
<template>
  <div id="switch__">
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="800px">
        <v-card>
          <v-card-title>
            <span class="headline">异常订单详情</span>
          </v-card-title>
          <v-card-text>

            <p class="root_p">
              <span class="order_title">操作用户ID</span>
              <v-chip>{{ data.id }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">订单ID</span>
              <v-chip>{{ data.operator_id }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">描述</span>
              <v-chip>{{ data.descriptor }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">异常参数</span>
              <!-- <v-chip>{{ data.content }}</v-chip> -->
              <span class="content__" > {{ data.content }} </span>
            </p>

            <p class="root_p">
              <span class="order_title">URL</span>
              <v-chip>{{ data.main_menu }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">时间</span>
              <v-chip>{{ data.create_time | time }}</v-chip>
            </p>

          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      data: {
        content: "订单异常,请检查!该订单已存在[5AOmU7hgOiCP5v3iXbHU3CCciXR6R45S1]",
        create_time: 1569057268,
        descriptor: "医生获取个人信息",
        id: 1,
        main_menu: "/api/v1/doctor/info",
        operator_id: 511
      }
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    /** 初始化数据 */
    info(data) {
      console.log( data );
      this.dialog = data.state;
      this.data = data.list;
    },
  }
};
</script>

<style lang="less">

#switch__ {
  .v-input__slot {
    margin-bottom: 0px !important;
  }
  .v-input--selection-controls {
    margin-top: 0px !important;
  }
  .v-list__tile__content {
    display: block !important;
  }
  .v-list__tile--avatar {
    min-height: 70px;
    display: inline-table;
  }
}
  .content__{
    display: inline-block;
    width: 500px;
    color: #3f51b5;
  }
.root_p {
  font-size: 16px;
  padding-left: 17px;
}
.order_title {
  display: inline-block;
  width: 125px;
}
</style>

