 * @Description: 异常订单日志
 * @Date: 2019-10-09 15:12:17
 * @LastEditors: Ji Yinghao
 * @LastEditTime: 2019-10-09 17:33:02
 -->
<template>
    <div class="oddOrder">
      <!-- 导航 -->
      <Breadcrumbs :items="items" />
      <Module2 ref="module2" />
      <!-- 表格 -->
      <v-data-table
        :headers="headers"
        :items="desserts.list"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props" >
          <tr @dblclick="infoMessage( props.item )">
            <td class="text-xs-centre"> {{ props.item.id }} </td>
            <td class="text-xs-centre"> {{ props.item.operator_id }} </td>
            <td class="text-xs-centre"> {{ props.item.descriptor }} </td>
            <td class="text-xs-centre"> {{ props.item.create_time | time }} </td>
            <td class="text-xs-centre">{{ props.item.content | slice }} </td>
            <td class="text-xs-centre"> {{ props.item.main_menu }} </td>
            <td class="text-xs-centre">
                <v-btn text icon @click="infoMessage( props.item )" >
                    <v-icon color="primary"  >visibility</v-icon>
                </v-btn>
            </td>
          </tr>
        </template>
        <template v-slot:no-data><p class="p_text">没有更多数据了 :(</p></template>
        <template v-slot:pageText="props" >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </div>
</template>

<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module2 from './components/module2';
export default {
    components: {
        Breadcrumbs,
        Module2
    },
    data(){
        return {
            // 导航数据
            items: [
                {
                text: "首页",
                disabled: false,
                to: "/home"
                },
                {
                text: "异常订单日志",
                disabled: false,
                to: "/oddOrder"
                }
            ],
            // 表格数据
            headers: [
                { text: "操作用户ID", value: "carbs", sortable: false },
                { text: "订单ID", value: "carbs", sortable: false },
                { text: "描述", align: "left", sortable: false, value: "name" },
                { text: "时间", align: "left", sortable: false, value: "name" },
                { text: "异常参数", value: "calories", sortable: false },
                { text: "URL", value: "fat", sortable: false },
                { text: "操作", value: "fat", sortable: false }
            ],
            // 数据列表
            desserts: {
                list: []
            },
            // 分页数据
            pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
            loader: false,
            allList: 0,
            page: 0,
            page_count: 10,
        }
    },
    methods: {
        aaa(){ console.log(123) },
        /* 异常订单列表 */
        info(event){
            this.loader = true;
            this.$axios({
                method: "post",
                url: this.$apis.URL.manages_query_fatal_log,
                data: {
                    page: event==undefined?this.page:event.page-1,
                    page_count: event==undefined?this.page_count:event.rowsPerPage
                }
            })
            .then(response => {
                if (response.data.result === 0) {
                    this.desserts = response.data.datas;
                    this.allList = response.data.datas.total_number;
                    this.loader = false;
                } else { this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true }); }
                this.loader = false;
            })
            .catch(error => {
                this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
            });
        },
        /* 异常订单详情 */
        infoMessage(list){
            let datas = {
                state: true,
                list: list
            };
            this.$refs.module2.info(datas);
        }
    }
}
</script>